@import "../utils/variables";
@import "../utils/mixins";

/*
  _________________________________________________________

  //! Phone Screen

*/

.phoneScreen__header {
  // height: 61px;
  background-color: $white;
  // background-image: url(../../shared/el/screens/phone/tchopArticle.png);
  box-shadow: 0 5px 10px -5px fade-out(#000,0.75), 0 2px 3px -2px fade-out(#000,0.75);
  position: relative;
  z-index: 10;
  // background-size: cover;
  width: 100%;
}

.phoneScreen__channelSwitcher {
  list-style-type: none;
  position: absolute;
  background-color: $white;
  left: 0;
  right: 0;
  display: flex;
  flex-flow: column nowrap;
  padding-left: 0;
  box-shadow: 0 10px 10px -5px fade-out(#000,0.75), 0 4px 4px -2px fade-out(#000,0.75);
  z-index: -1;
  & > li {
    text-align: center;
    @include padding(y,1);
    & + li {
      border-top: 1px solid fade-out($body-color, 0.85);
    }
  }

  .channel__title {
    color: $gray-600;
  }
}


.phoneScreen__dimmer {
  position: absolute;
  background-color: #000;
  opacity: 0.75;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
}

.phoneScreen--animated,
.phoneScreen--stream {
  // background-color: $gray-700; // darken($yellow,15%);
  display: flex;
  flex-flow: column nowrap;
  height: 100%;
  overflow: hidden;
  position: relative;
  box-shadow: inset 0 0 15px fade-out(#000,0.9);

  // @include padding(top,0.5);
  @include padding(x,.75);

  .slide {

  }

  .phoneContent {
    background-color: $white;

    // @include margin(bottom,1);
    // @include margin(x,1);
  }

  .phoneContent__image {
    @include margin(x,-1);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }

  .phoneContent__content {
    // border-bottom: 1px solid $orange;
  }



  .slide--img {
    @include padding(top,.5);


    img {
      max-width: 100%;
      width: auto;
      box-shadow: 0 5px 8px -5px fade-out(#000,.8);
      border-radius: 5px;
    }
  }

  .slide--chat {
    position: absolute;
    top: 0;
    left: 100%;
    @include padding(right,4);
    // width: 90%;
    // box-sizing: border-box;
    // background-color: darken($blue,15%);

  }

}

.slide--chat {
  width: 78%;
  height: 100%;
  box-sizing: border-box;
  @include padding(top,1);
  @include padding(x,1);
  // background-color: $gray-300;
  @include media-breakpoint-up(sm) {
    @include padding(x,2);
    @include padding(top,1);
    width: 100%;
  }
}


.chat__item {

  @include margin(top,0.75);

  @include media-breakpoint-up(sm) {
    @include margin(top,1);
  }

  &:nth-child(even) {
    @include margin(left,2);
    .chat__author {
      text-align: right;
      .lipsum-bar {
        &:before {

          background-color: $orange;
          // margin-left: -22px;
        }
      }
    }
    .chat__bubble {
      /*
      &:after {
        right: 30px;
        left: auto;
        border-left-color: transparent;
        border-right-color: $white;
      }
      */
    }
  }
  &:nth-child(odd) {
    @include margin(right,2);
  }
}
.chat__bubble {

  background-color: $white;
  @include padding(x,1);
  @include padding(y,0.75);
  border-radius: $border-radius-lg; // 10px;
  position: relative;
  box-shadow: 0 0 8px -4px fade-out(#000,0.75), 1px 1px 3px -2px fade-out(#000,0.75);

  @include media-breakpoint-up(sm) {
    @include padding(x,2);
    @include padding(y,1);
  }


  .lipsum-bars {
    line-height: 0.8;
    @include media-breakpoint-up(sm) {
      line-height: inherit;
    }
  }
  .lipsum-bar {
    height: 6px;

    @include media-breakpoint-up(sm) {
      height: 10px
    }
  }

  /*
  &:after {
    content: "";
    border: 14px solid transparent;
    border-left-color: $white;
    bottom: -14px;
    left: 30px;
    right: auto;
    position: absolute;
  }
  */
}

.chat__author {
  .lipsum-bar {
    background-color: fade-out($body-color,.6);
    max-width: 40px;
    height: 6px;
    @include margin(top,0.75);
    margin-left: 22px;

    @include media-breakpoint-up(sm) {
      max-width: 60px;
      height: 8px;
      margin-left: 18px;
    }

    &:before {
      content: "";
      width: 12px;
      height: 12px;
      background-color: $green;
      position: absolute;
      display: block;
      border-radius: 50%;
      margin-left: -22px;
      margin-top: -3px;

      @include media-breakpoint-up(sm) {
        width: 16px;
        height: 16px;
        margin-left: -18px;
      }
    }
  }
}