@import "../utils/variables";
@import "../utils/mixins";

.btn {
  @include small-caps();
  font-size: $font-size-lg;
  letter-spacing: .1em;
  padding: 0.325rem 1rem 0.425rem;
  @include margin(top,1.5);
  @include margin(bottom,.5);
}

.btn--primary {
  background-color: $orange;
  color: $body-bg;
}