@import "../utils/variables";
@import "../utils/mixins";


.pageHeader {

  @include padding(y,3);
  @include margin(bottom,2);

  h1 {
    font-size: $h2-font-size;
    font-weight: bold;
    @include margin(bottom,1.5);

    @include media-breakpoint-up(sm) {
      font-size: $h1-font-size;
    }

    @include media-breakpoint-up(lg) {
      font-size: $display4-size;
    }

    .trademark {
      font-size: $font-size-base;
      vertical-align: super;
    }

    .lead {
      font-size: $font-size-lg;
    }

    & + .page__section__headline {
      @include margin(top,-1);
      @include margin(bottom,2);
    }
  }

  .breadcrumb + h1 {
    @include margin(top,-1);
  }

}


.pageHeader__row {
  @include make-row();
}

.headerCol--image,
.headerCol--content {
  @include make-col-ready();

}


.headerCol--image {
  order: -1;
  align-self: flex-end;
  @include margin(bottom,1);
  @include media-breakpoint-up(sm) {
    order: inherit;
    @include make-col(6);
    @include margin(bottom,0);
  }
  // @include media-breakpoint-up(lg) {
  //   @include make-col(6);
  // }
}

.headerCol--content {

  @include make-col(10);
  @include make-col-offset(2);

  @include media-breakpoint-up(sm) {
    @include make-col(6);
    @include make-col-offset(0);
  }
  // @include media-breakpoint-up(lg) {
  //   @include make-col(6);
  // }
}

.col__cnt-img,
.col__img-cnt {
  .headerCol--image {
    @include media-breakpoint-up(sm) {
      @include make-col(4);
    }
  }
  .headerCol--content {
    @include media-breakpoint-up(sm) {
      @include make-col(8);
    }
  }
}

.col__cnt-img {
  .headerCol--image {
    order: -1;
    @include media-breakpoint-up(sm) {
      order: inherit;
    }
    @include media-breakpoint-up(xl) {
      @include make-col-offset(2);
    }
  }

  .headerCol--content {
    @include media-breakpoint-up(xl) {
      @include make-col(6);
    }
  }

}

.col__img-cnt {
  .headerCol--content {
    @include media-breakpoint-up(xl) {
      @include make-col(6);
      @include make-col-offset(2);
    }
  }
}


.pageHeader, .page__section {
   @include media-breakpoint-up(xl) {
    font-size: $font-size-lg;
  }
}


.page--backend {
  .headerCol--image {
    display: flex;

    @include media-breakpoint-up(xl) {
      align-items: flex-end;
    }
  }
}






/*
  _________________________________________________________

  //! Page section

*/

.page__section {
  @include padding(y,4);
  border-top: $light-border;
  @include media-breakpoint-up(sm) {
    @include padding(y,8);
  }
}


// Layout

.page__section__row {
  @include make-row();
  align-items: flex-end;

}

.page__section__coltitle,
.page__section__colimage,
.page__section__colcontent {
  @include make-col-ready();
}

.page__section__coltitle {

  @include make-col(10);
  @include make-col-offset(2);

  @include media-breakpoint-up(sm) {
    @include make-col(4);
    @include make-col-offset(0);
    @include padding(right,3);

  }

  .page__section__headline {
    @include margin(bottom,0);
  }
}

.page__section__colimage {
  order: -1;
  @include margin(bottom,2);
  @include media-breakpoint-up(sm) {
    @include make-col(8);
    order: inherit;
    @include margin(bottom,0);
  }
}

.page__section__colcontent {

  @include margin(top,1.5);
  @include make-col(10);
  @include make-col-offset(2);

  @include media-breakpoint-up(sm) {
    @include margin(top,4);
    @include make-col(8);
    @include make-col-offset(4);
  }

  &.no-offset {
    @include margin(top,1);
    @include media-breakpoint-up(sm) {
      margin-left: inherit;
      @include margin(top,0);
    }
  }
}



body {
  counter-reset: step-counter;
}
.page__section__headline {
  @include margin(bottom,1);
  font-size: $h3-font-size;

  @include media-breakpoint-up(lg) {
    font-size: $h2-font-size;
  }

  &.numbered {

    position: relative;

    @include media-breakpoint-up(sm) {
      @include padding(left,2);
    }
    &:before {
      counter-increment: step-counter;
      content: counter(step-counter) ".";
      position: absolute;
      @include margin(left,-2);
      font-weight: bold;
    }
  }
}

.page__section__header {
  @include margin(bottom,2);
}

.page__section--footer {
  min-height: 100vh;
  @include padding(y,0);
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  border-top: none;
}




.nextPageDisclaimer {
  text-align: center;
  .next {
    @include small-caps;
  }
}

.nextPageLink {
  display: block;
  @include margin(top,1);
  // color: $blue;
  &:hover {
    text-decoration: none;
    color: $yellow;
    // color: $orange;
  }
  .title {
    font-size: $h2-font-size;
    font-weight: bold;
    line-height: $headings-line-height;
    @include margin(bottom,.5);
    @include media-breakpoint-up(sm) {
      font-size: $h1-font-size;
    }
  }
}


.panel--stepbystep {
  @include padding(y,4);

  // & + .panel--stepbystep {
  //   border-top: $light-border;
  // }
}

.stepbystep {
  display: flex;
}

.sbs__number {
  font-size: $font-size-lg; // 3.5rem;
  font-weight: bold;
  // line-height: 1;
  flex: 0 0 40px;
}

.sbs__content {
  font-size: $font-size-lg;
}


.featureIconList {
  $icon-size-xs: 40px;
  $icon-size: 60px;

  @mixin gradient($color) {
    background-color: $color;
    background-image: linear-gradient(-15deg, darken(adjust-hue($color,10),10%), $color);
  }

  list-style-type: none;
  padding-left: 0;
  display: grid; // flex;
  grid-template-columns: 1fr;
  grid-column-gap: $grid-gutter-width;
  grid-row-gap: 1rem;
  justify-items: start;
  @include margin(y,3);
  // flex-flow: row wrap;

  @include media-breakpoint-up(sm) {
    grid-template-columns: repeat(2,1fr);
    grid-row-gap: 2rem;
  }

  .icon {

    width: $icon-size-xs;
    height: $icon-size-xs;
    // background-color: $gray-600; // $blue; // $gray-200;
    @include gradient($gray-700);
    @include margin(x,auto);
    // @include margin(bottom,1);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    @include margin(right,1);
    flex: 0 0 $icon-size-xs;

    @include media-breakpoint-up(sm) {
      width: $icon-size;
      height: $icon-size;
      flex: 0 0 $icon-size;
    }



    &:before {
      font-size: $icon-size-xs * 0.5; // 36px;
      color: $white;

      @include media-breakpoint-up(sm) {
        font-size: $icon-size * 0.5; // 36px;
      }
    }
  }

  &.green {
    .icon {
      @include gradient($green);
    }
  }

  &.orange {
    .icon {
      @include gradient($orange);
    }
  }

  &.blue {
    .icon {
      @include gradient(desaturate($blue, 20%));
    }
  }


  & > li {
    // text-align: center;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    // font-weight: bold;
  }

  &.icons--top {
    .icon {
      @include margin(top,-1);
    }
    & > li {
      align-items: flex-start;



    }
  }

}




.plane {
  background-color: $gray-100;
  @include padding(x,1);
  @include margin(x,-1);
  @include padding(top,0.75);
  @include margin(top,2);
  display: inline-block;
  border-radius: $border-radius;
}

.link--large {
  font-size: $h2-font-size;
  &:hover {
    text-decoration: none;
    color: $red;
  }
}





