}


/*
  _________________________________________________________

  //! Snippets list

*/

.list--snippets {
  list-style-type: none;
  padding-left: 0;
}

.list--snippets__item {
  display: flex;
  @include margin(top,3);
  .snippet {
    flex: 0 0 60px;
    @include margin(top,-.75);
    @include margin(right, 1);
    margin-left: -30px;

    @include media-breakpoint-up(sm) {
      flex-basis: 100px;
      margin-left: 0;
    }
  }
}

