@import "variables.bs";
@import "../../node_modules/sass-easing/_easings";

$header-height:    80px;
$header-height-xs: 60px;

$font-family-phoneui:      -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol" !default;
$font-family-meta:         'Pangolin', 'Comic Sans', cursive;

$light-border-width: 1px;
$light-border: $light-border-width solid fade-out(#000,.75);

$widget-color: $red; // $gray-700;

$cardBorderRadius: 4px;

$meta-border-width: 4px; // 2px;
$meta-arrow-size: $meta-border-width * 6;

$meta-color: $yellow; // lighten($blue,20%); // $white;
$bg-blue: desaturate(darken($blue,20%), 20%);

$radio-size: 20px;
$radio-size-xs: 16px;

$bullet-size: 3px;
$bullet-active-size: 10px;
$bullet-margin: 10px;

$offcanvas-width: 220px;
$offcanvas-trans-duration: 0.3s;
$offcanvas-trans-default: $offcanvas-trans-duration transform $easeOutSine;

$spacer: 1rem !default;
