@import "../utils/variables";
@import "../utils/mixins";

.siteHeader {
  background-color: $white; // $gray-100; // $yellow;

  position: fixed;
  z-index: $zindex-fixed + 10;
  left: 0;
  right: 0;
  top: 0;

  & > .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: $header-height-xs;
    @include padding(y,1);
    border-bottom: $light-border;
    position: relative;

    @include media-breakpoint-up(sm) {
      height: $header-height;

    }
  }
}


.nav--main  {
  display: none;
  align-items: center;
  @include margin(right,1);
  @include media-breakpoint-up(sm) {
    display: flex;
  }
  .linkItem {

    font-size: $font-size-lg;




    & + .linkItem {
      margin-left: 1em;
    }
  }

  .linkItem--btn {
    & + .linkItem--btn {
      margin-left: 0.2em;
    }
  }
}

.linkItem--btn {
  border: $light-border;
  border-radius: $border-radius;
  @include padding(x,1);
  @include padding(y,.25);
  // @include small-caps();
}

.icon--burger {
  margin-left: 1em;
  .bar {
    background-color: $body-color;
    width: 40px;
    height: 4px;
    margin: 7px auto;
    border-radius: 2px;
  }
}

.siteLogo {
  width: 100px;
  @include media-breakpoint-up(sm) {
      width: 140px;
  }
  svg {
    width: auto;
    height: auto;
    max-width: 100%;
  }
}

.siteNav {
  display: flex;
  align-items: center;
}


