@import "../utils/variables";
@import "../utils/mixins";

.colorWrapper {
  // background-color: fade-out($blue,.8);
  // position: relative;
  &:after {
    content: "";
    min-height: 1px;
    display: block;
    width: 100%;
    margin-top: -1px;
  }
}

#phone {
  display: none;
  @include media-breakpoint-up(sm) {
    display: block;
  }
}

.phone {
  position: fixed;
  z-index: $zindex-fixed;
  left: auto;
  right: -80px;
  bottom: 50%; // 160px;
  // top: $header-height + 20px;
  pointer-events: none;
  max-width: 200px; // 480px;
  transform: translateY(50%);

  @include media-breakpoint-up(sm) {
    max-width: 330px; // 480px;
    transform: translateX(140px);
    // top: auto;
    bottom: -60px;
    right: auto;
  }

  @include media-breakpoint-up(xl) {
    max-width: 400px;
    bottom: -160px;
    transform: translateX(170px);
    left: 50%;
  }

  @media only screen and (min-height: 960px) {
    bottom: auto;
    top: 50%;
    transform: translateX(170px) translateY(-50%);
  }

  &:before {
    content: "";
    position: absolute;
    top: 5px;
    right: 5px;
    bottom: 5px;
    left: 5px;
    border-radius: 50px;
    box-shadow: 0 30px 80px -5px fade-out(#000,.7), 0 20px 20px fade-out(#000,.6);
  }
}





.swiper--phone--screens {

  width: 96%;
  height: 344px;
  margin-top: 30px;


  @include media-breakpoint-up(sm) {
    width: 96%;
    height: 567px;
    margin-top: 50px;
  }

  @include media-breakpoint-up(xl) {
    margin-top: 61px;
    width: 379px;
    height: 695px;
    margin-left: 10px;
  }
}

.swiper__layer--phone {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  img {
    max-width: 100%;
    @include media-breakpoint-up(xl) {
      max-width: none;
    }
  }
}






// __________________________________________________
//
//
//			!Phone Content Swiper


.swiper--phoneScreen-01 {
  width: 384px;
  height: 600px;
  background: darken($yellow,10%);
  // flex-direction: column-reverse;
  & > .swiper-wrapper {
    // flex-direction: column-reverse;
  }
  .swiper-slide {
    background-color: #fff;
    height: auto;
  }
}

.phoneContent {
}

.phoneContent__content {
  padding: $grid-gutter-width;


  .tagline {
    font-size: $font-size-sm;
    @include small-caps;
    color: $orange;

    font-weight: bold;
    @include margin(bottom,.25);
    @include media-breakpoint-up(xl) {
      font-size: $font-size-sm;
    }
  }

  h3 {
    font-size: $font-size-lg;

    @include media-breakpoint-up(xl) {
      font-size: $h3-font-size;
    }
  }

  .excerpt {
    opacity: .8;
    font-size: $font-size-sm;

    @include margin(top,1);
    @include media-breakpoint-up(xl) {
      font-size: $font-size-base;
    }
  }

}

.phoneContent__image {
  background-color: $orange;
  min-height: 160px;
}





// __________________________________________________
//
//
//			!Screens


.phoneScreen {
  position: relative;
  height: 100%;
  width: 100%;
  box-sizing: content-box;

  .placeholder {
    position: relative;
    // color: $white;
    // width: 100%;
    min-height: 400px;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    @include padding(top,4);
    @include padding(x,2);

    ul {
      list-style-type: none;
      li {
       @include padding(left,1.5);
       position: relative;
        &:before {
          content: "\2192";
          position: absolute;
          @include margin(left,-1.5);
          margin-top: 1px;
        }
      }
    }
  }
}

.phoneScreenSlideWrapper {
  position: relative;
  // width: 100%;
  height: 100%;
  display: flex;
  // overflow: hidden;
  transform: translateX(0);

}
.phoneScreenSlide {
  position: relative;
  width: 100%;
  height: 100%;
  flex: 0 0 100%;
  overflow: hidden;
}


.phoneScreen__background {
  background: linear-gradient(160deg, $gray-800, $gray-700);
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.phoneScreen__background--app {
  background: linear-gradient(160deg, $gray-400, $gray-300); // linear-gradient(160deg, $gray-600, $gray-400); // linear-gradient(160deg, $gray-400, $gray-100);
}




// locked screen

.phoneScreen__background--locked,
.phoneScreen__background--home {
  background: linear-gradient(160deg, $gray-800, $gray-700);
  // background-image: url(../../shared/el/screens/phone/locked_bg.jpg);
  // background-size: cover;
}

.phoneScreen__background--home {
  // background-image: none;
  // background-color: $orange;
  // background-blend-mode: multiply;
}

.lockedScreen--dateTime {
  font-family: $font-family-phoneui;
  color: fade-out($white,.3);
  display: flex;
  flex-flow: column nowrap;
  width: 100%;
  align-items: center;
  text-align: center;
  @include padding(top,1.5);
  @include media-breakpoint-up(sm) {
    @include padding(top,4);
  }
}

.lockedScreen--time {
  font-size: 2.5rem;
  font-weight: 200;
  line-height: 1.1;

  @include media-breakpoint-up(sm) {
    font-size: 6rem;

  }
}

.lockedScreen--date {
  font-size: 11px;
  @include media-breakpoint-up(sm) {
      font-size: $font-size-sm;
  }
}








.phoneScreen--push {
  $radius: 6px;

  .pushMessage {
    font-family: $font-family-phoneui;
    position: absolute;
    left: 50%;
    top: 50%;
    width: 80%;
    opacity: 0;
    transform: translateX(-50%) translateY(-50%) scale(.5);
    background-color: fade-out($gray-100,.1);
    border-radius: $radius;
    box-shadow: 0 0 30px fade-out(#000,.5), 0 0 4px -1px fade-out(#000,.5);

    @include media-breakpoint-up(sm) {

    }
  }

  .pushMessage__header {
    @include padding(left,.5);
    @include padding(y,.5);
    background-color: $gray-100;
    color: $gray-800;
    display: flex;
    align-items: center;
    border-radius: $radius $radius 0 0;
    .logo {
      @include margin(right,.6);

      img {
        max-width: 20px;
        height: auto;

        @include media-breakpoint-up(sm) {
          max-width: 26px;
        }
      }
    }
    .title {
      font-weight: bold;
      text-transform: uppercase;
      font-size: 11px;
      @include media-breakpoint-up(sm) {
        font-size: $font-size-sm;
      }
    }
  }
  .pushMessage__body {
    @include padding(x,1);
    // @include padding(right,2);
    @include padding(top,0.75);
    @include padding(bottom,1);

    @include media-breakpoint-up(sm) {
      @include padding(x,1.5);
      @include padding(top,1);
      @include padding(bottom,1.5);
    }
  }
  .img--push {
    position: absolute;
    left: 50%;
    top: 50%;
    max-width: 80%;
    opacity: 0;
    transform: translateX(-50%) translateY(-50%) scale(.5);
    // transition: 0.5s transform $easeOutBack, 0.5s opacity $easeOutQuad;
  }

  .lipsum-bar {}
}

.phoneScreen--appicon {
  background-color: $blue;
  background: linear-gradient(160deg, darken($blue,10%), darken($blue,40%));
  display: flex;
  align-items: center;
  justify-content: center;

  .img--appicon {
    max-width: 100px;
    height: auto;
    @include margin(top,-2);
  }

}

.phoneScreen.slide--chat {
  // background: linear-gradient(160deg, darken($blue,10%), darken($blue,40%));
}

.phoneScreen--case {
  background-color: #000;
  img {
    opacity: 0.9;
  }
}




