@import "../utils/variables";
@import "../utils/mixins";

picture.img-fluid img,
picture.img-fluid source {
  max-width: 100%;
  height: auto;
}

.buttonRow {
  display: flex;

  @include margin(top,2);

  flex-flow: column wrap;

  @include media-breakpoint-up(sm) {
    flex-flow: row nowrap;
    align-items: center;
  }

  .btn {
    margin-right: 1em;
    margin-top: 0;
    @include margin(bottom, 0.5);
    @include media-breakpoint-up(sm) {
      margin-bottom: 0;
    }
  }
  .link--more {
    display: inline;
    margin-top: 0;
  }
}



.clientLogos {


  .small {
    font-size: 11px;
  }

  .logos {
    @include margin(top,1);
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    width: percentage(7/12);

    @include media-breakpoint-up(sm) {

    }



    img {
      width: 65px;
      margin-right: 1em;
      margin-bottom: 0.75em;
      @include media-breakpoint-up(sm) {
        width: 90px;
        margin-right: 1.5em;
      }
    }

    .logo--fa {
      width: 85px;
      @include media-breakpoint-up(sm) {
        width: 120px;
      }
    }

    .logo--unicef {
      margin-top: -4px;
    }

    .logo--total {
      margin-top: -4px;
    }

    .logo--hb {
      margin-top: -4px;
      width: 80px;
      @include media-breakpoint-up(sm) {
        width: 100px;
      }
    }
  }


}


.widget__title {
  color: $widget-color;
}


.timeline {
  display: flex;
  @include margin(x,-0.5);
  // justify-content: space-between;
  // width: 100%;
  position: relative;



  .timeline__item {
    // border: $light-border;
