@import "variables";

// Margins spacer system

@mixin margin($param, $factor) {
  $value: auto !default;

  @if($factor == auto) {
    $value: auto;
  } @else {
    $value: $spacer * $factor;
  }

  @each $p in (top,right,bottom,left) {
    @if($param == $p) {
      margin-#{$param}: $value;
    }
  }

  @if($param == x) {
    margin-left: $value;
    margin-right: $value;
  }

  @if($param == y) {
    margin-top: $value;
    margin-bottom: $value;
  }

}

// Padding spacer system

@mixin padding($param, $factor) {
  $value: $spacer * $factor;

  @each $p in (top, right, bottom, left) {
    @if ($param == $p) {
      padding-#{$param}: $value;
    }
  }

  @if ($param == x) {
    padding-left: $value;
    padding-right: $value;
  }

  @if ($param == y) {
    padding-top: $value;
    padding-bottom: $value;
  }
}


/*
  _________________________________________________________

  //! Mixins

*/

@mixin small-caps() {
  font-variant-caps: all-small-caps;
  font-size: $font-size-sm;
}

@mixin fill-browser {
  position: absolute;
  width: 696px;
  height: 407px;
  top: 32px;
  left: 2px;
  background-color: fade-out($yellow, 0.6);
}

@mixin fill-abs {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

@mixin small-caps-f() {
  font-variant-caps: all-small-caps;
  font-variant: small-caps;
}
