$font-name: 'Basier Circle' !default;
$font-folder: './fonts/basier-circle/'; // '../../files/fonts/';

@mixin font-face($font-name, $font-file-prefix, $weight, $style) {
  @font-face {
    font-family: $font-name;
    src: url($font-folder + $font-file-prefix + '.eot');
    src: url($font-folder + $font-file-prefix + '.eot?#iefix') format('embedded-opentype'),
         url($font-folder + $font-file-prefix + '.woff2') format('woff2'),
         url($font-folder + $font-file-prefix + '.woff') format('woff'),
         url($font-folder + $font-file-prefix + '.ttf') format('truetype');
    font-weight: $weight;
    font-style: $style;
  }
}

// default
@include font-face($font-name, 'basiercircle-regular-webfont', 400, normal);
@include font-face($font-name, 'basiercircle-regularitalic-webfont', 400, oblique);

// medium
@include font-face($font-name, 'basiercircle-medium-webfont', 600, normal);
@include font-face($font-name, 'basiercircle-mediumitalic-webfont', 600, oblique);

// semibold
@include font-face($font-name, 'basiercircle-semibold-webfont', 700, normal);
@include font-face($font-name, 'basiercircle-semibolditalic-webfont', 700, oblique);


