@import "../utils/variables";
@import "../utils/mixins";

.tchopCard {
  background-color: $white;
  @include margin(top,0.75);
  border-radius: $border-radius-lg;
  box-shadow: 0 5px 8px -5px fade-out(#000,.8), 0 0 3px -1px fade-out(#000,.5);

}

.tchopCard__image {

  position: relative;

  img {
    border-radius: $border-radius-lg $border-radius-lg 0 0;
  }

  &.colorized {
    // background-color: $yellow;
    .tchopCard__thumbnail {
      // mix-blend-mode: multiply;
    }
  }

  .icon--video {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    mix-blend-mode: normal;
    width: 40px;
    @include media-breakpoint-up(sm) {
      width: 60px;
    }
  }
}

.tchopCard__socialHeader {
  display: flex;
  align-items: center;
  @include margin(bottom,0.5);
  .icon--social {
    width: 20px;
    height: 20px;
    margin-left: -8px;

    @include media-breakpoint-up(sm) {
      width: 30px;
      height: 30px;
      margin-left: -15px;
    }
  }
  .tchopCard__socialAuthor {
    flex-basis: 40px;
    margin-left: 10px;
    line-height: 0.44;
    @include media-breakpoint-up(sm) {
      line-height: 0.7;
      flex-basis: 60px;
    }
    .lipsum-bar {
      height: 4px;
      @include media-breakpoint-up(sm) {
        height: 6px;
      }
      &:first-child {
        background-color: $gray-600;
      }
    }
  }
}

.tchopCard__content {
  @include padding(x,1);
  @include padding(top,0.5);
  @include padding(bottom,1);
  @include media-breakpoint-up(sm) {
    @include padding(x,1.6);
    @include padding(top,1);
    @include padding(bottom,1.5);
  }

}

.tchopCard__tagline.lipsum-bar {
  background-color: $orange;
  height: 4px;
  width: 60px;
  @include media-breakpoint-up(sm) {
    height: 6px;
    width: 80px;

  }
}

.tchopCard__excerpt.lipsum-bars {
  line-height: 0.6  ;
  @include media-breakpoint-up(sm) {
    line-height: 1;
  }
  .lipsum-bar {
    height: 4px;
    background-color: $gray-400;
    @include media-breakpoint-up(sm) {
      height: 7px;
    }
  }
}

.tchopCard__heading.lipsum-bars {
  line-height: 0.6;
  @include margin(bottom,0.5);
  @include media-breakpoint-up(sm) {
      line-height: 0.9;

  }
  .lipsum-bar {
    height: 6px;
    background-color: $gray-600;

    @include media-breakpoint-up(sm) {
      height: 10px;
    }
  }
}


/*
  _________________________________________________________

  //! App Header

*/

.phoneScreen__header__inner {
  display: flex;
  background-color: $white;
  min-height: 40px;
  @include padding(x,0.5);
  justify-content: space-between;
  align-items: center;
  font-family: $font-family-phoneui;

  @include media-breakpoint-up(sm) {
    min-height: 60px;
    @include padding(x,1);
  }
}




.phoneScreen__header__back {
  width: 10px;
  @include media-breakpoint-up(sm) {
    width: 15px;
  }
}

.phoneScreen__header__logo img {
  width: 20px;
  height: auto;
  @include media-breakpoint-up(sm) {
    width: 30px;
  }
}

.phoneScreen__header__sectiontitle {

  font-size: 11px;

  @include media-breakpoint-up(sm) {
    font-size: $font-size-sm;
  }
  .icon {
    margin-left: 0.3em;
    vertical-align: 1px;

  }
}


/*
  _________________________________________________________

  //! Article

*/



.tchopApp__article {
  background-color: $white;
  min-height: 100%;
}

.tchopApp__article__image {
  position: relative;
  .textOnImage {
    position: absolute;
    top: 0;
    right: auto;
    bottom: 0;
    left: 0;
    width: 50%;
    display: flex;

    align-items: center;
    color: $white;
    font-weight: bold;
    // font-family: $font-family-serif;
    font-size: 2rem;
    letter-spacing: -0.03em;
    font-variant-caps: all-small-caps;
    @include padding(x,1);
    line-height: 0.7;
    background-color: fade-out(#000,0.9);
    opacity: 0.9;

    @include media-breakpoint-up(sm) {
      font-size: 6rem;
      justify-content: center;
      text-align: center;
      @include padding(x,2);
      right: 0;
      width: auto;
    }

    &.top {
      align-items: flex-start;
      @include padding(top,2);
    }
  }
}

.tchopApp__article__content {
  @include padding(x,1);
  @include padding(top,1);
  @include media-breakpoint-up(sm) {
    @include padding(x,2);
    @include padding(top,1.5);
  }
}

.tchopApp__article__title {
  line-height: 1.2;
  @include margin(bottom,0.5);
  @include media-breakpoint-up(sm) {
    @include margin(bottom,1);
  }
  .lipsum-bar {
    background-color: $gray-600;
    @include media-breakpoint-up(sm) {
      height: 12px;
    }

  }
}

.tchopApp__article__body {
  line-height: 1.3;
  .lipsum-bar {
    background-color: $gray-300;
  }
}