@import "../utils/variables";
@import "../utils/mixins";

.swiper--snippets {
  max-width: 800px;
  @include margin(top,2);
  z-index: 1;
  .swiper-slide {
    // max-width: 400px;
    // background: fade-out(#000,.8);

  }

  .swiper-slide-active,
  .swiper-slide-prev
   {
    // background: fade-out(#000,.8);
    .snippet {
      transform: translateX(600px);
      transition: .5s transform $easeInQuad .3s;

      &.snippet--delay {
        transition: .3s transform $easeInQuad .5s;
      }
    }
  }
}

.slide--snippet {

  .snippet {

    width: auto;
    max-width: 100%;
    // display: inline-block;
  }
}

.snippetContainer {
  // display: flex;
  // flex-flow: column nowrap;
}

.slide {
  &--video {
    max-width: 300px;
  }
  &--social {
    max-width: 100px;
  }
  &--web {
    max-width: 520px;
  }
}

.snippet--news {
  margin-right: 80px;
}

.snippet--link {
  margin-left: 80px;
}

.snippet--video {
  margin-top: 110px;
}