@import "../utils/variables";
@import "../utils/mixins";

.icon--burger {
  .bar {
    transition: $offcanvas-trans-duration transform $easeOutBack;
  }

}

[data-toggle-navlayer] {
  transition: 0.3s transform $easeOutBack;
  &:hover {
    .bar {
      &:nth-child(1) {
        transform: translateY(-3px);
      }
      &:nth-child(3) {
        transform: translateY(3px);
      }
    }
  }
}


.nav--main {
  transition: $offcanvas-trans-default;
}

html.navlayer--open {

  body {
    overflow: hidden;
  }

  #navLayer {
    transform: translateY(100vh);
    overflow: scroll;
    // opacity: 1;
  }

  .nav--main {
    transform: translateY(-100px);
  }

  .icon--burger {
    $offset: 7.5px;
    .bar {
      &:nth-child(1) {
        transform: rotate(45deg) translate($offset,$offset);
      }
      &:nth-child(3) {
        transform: rotate(-45deg) translate($offset,-$offset);
      }
      &:nth-child(2) {
        opacity: 0;
      }
    }
  }

  [data-toggle-navlayer] {
    &:hover {
      transform: scale(0.8);
    }
  }

}


#navLayer {
  position: absolute;
  left: 0;
  right: 0;
  height: 100vh; // calc(100vh - #{$header-height});
  top: -100vh;
  transform: translateY(0);
  background-color: $orange;
  // backdrop-filter: blur(15px) contrast(.3) brightness(1.5);
  // opacity: 0;
  transition: 0.3s transform $easeInQuad; // , 0.3s opacity $easeOutQuad;
  // height: 0;
  z-index: 0;
  padding-top: $header-height-xs + 40;
  color: $gray-800;
  overflow: hidden;

  @include media-breakpoint-up(sm) {
    padding-top: $header-height + 20;
  }


  a {
    // color: $white;
  }

  & > .container {

    height: 100%;

    & > .inner {
      display: flex;
      // align-items: center;
      flex-flow: column nowrap;
      justify-content: center; // space-between;
      height: 100%;

    }
  }

  .linkItem {
    white-space: nowrap;
    color: $gray-base;
    &:hover {
      color: $white;
      text-decoration: none;

    }
  }

  .btn--primary {
    color: $orange;
    background-color: $gray-800;
  }

  .buttonRow {
    @include margin(top,3);
  }
}

.navLayer__navMain {

  // @include margin(bottom,2);
  margin-top: auto;
  margin-bottom: auto;
  font-size: $h3-font-size;



  nav {
    display: flex;
    flex-flow: column nowrap;


  }
  .linkItem {
    font-size: $h4-font-size;



    @include media-breakpoint-up(sm) {
      font-size: $h2-font-size;
    }
  }

  .nav__heading {
    @include margin(bottom,1);
  }


  .icons--social {
    width: 140px;
    @include margin(x,auto);
    @include margin(top,2);
    path {
      fill: $body-color;
    }
  }
}

.navLayer__navSecondary {

  @include margin(bottom,4);
  font-size: $font-size-base;

  @include media-breakpoint-up(sm) {
    font-size: $h3-font-size;
  }

  nav {
    line-height: 2;
    display: flex;
    flex-flow: column nowrap;
    @include margin(bottom,2);
    // justify-content: center;
  }
  .linkItem,
  .nav__heading {
    // font-size: $h4-font-size;

  }
}


.nav__heading {
  font-weight: 600;
  @include small-caps();
  font-size:$font-size-sm;

}

.nav--ordered {
  $bullet-size: 20px;
  counter-reset: nav-counter;
  .linkItem {
    display: flex;
    align-items: center;
    &:before {
      content: counter(nav-counter);
	    counter-increment: nav-counter;
	    font-size: $font-size-sm;
	    @include margin(right,.5);
	    font-weight: 600;
	    line-height: 1em;
	    margin-top: -1em;

	    // color: $orange;
	    // background-color: $gray-800;
	    // width: $bullet-size;
	    // height: $bullet-size;
	    // line-height: $bullet-size;
	    // font-weight: bold;
	    //
	    // text-align: center;
	    //
	    // border-radius: 50%;
	    // display: flex;
	    // align-items: center;
	    // justify-content: center;


    }
  }
}




.navLayer__navSocial {
  & > nav {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    @include margin(x,-0.5);
  }

  .linkItem {
     @include padding(x,0.5);
     @include padding(y,0.5);
  }
}
