@import "../utils/variables";
@import "../utils/mixins";

#intercomLauncher {
  position: fixed;
  right: $grid-gutter-width / 2;
  bottom: $grid-gutter-width / 2;
  z-index: $zindex-fixed;

  @include media-breakpoint-up(sm) {
    right: $grid-gutter-width;
    bottom: $grid-gutter-width;
  }

  img {
    width: 40px;
    height: auto;
    @include media-breakpoint-up(sm) {
      width: 60px;
    }
  }
}