@import "../utils/variables";
@import "../utils/mixins";

.panel {
  display: flex;
  flex-flow: column nowrap; // row wrap;
  justify-content: space-between;
}

.panel--fullscreen {
  min-height: calc(100vh - #{$header-height-xs});
  align-items: center;
  margin-bottom: 33vh; // 50vh;
  @include media-breakpoint-up(sm) {
    min-height: calc(100vh - #{$header-height});

  }
}


.panel--largetype {
  margin-bottom: 0;

  .panel__inner {
    justify-content: center;
    flex-flow: column nowrap;
    width: 100%;
  }
  .panel__heading {
    font-size: 1.8rem;
    // font-weight: bold;
    text-align: center;
    @include media-breakpoint-up(sm) {
      font-size: 4rem;
    }
    @include media-breakpoint-up(xl) {

    }
  }
}


/*
  _________________________________________________________

  //! Panel pagination

*/

$bullet-size: 3px;
$bullet-active-size: 10px;
$bullet-margin: 10px;



#panelPagination {
  position: fixed;
  right: $grid-gutter-width / 2;
  z-index: $zindex-fixed;
  top: 50%;
  min-width: $bullet-active-size;
  transform: translateY(-50%);

  flex-flow: column nowrap;
  justify-content: space-between;
  align-items: center;
  display: none;
  @include media-breakpoint-up(sm) {
    display: flex;
  }
}



.panelPagination__bullet {


  background-color: $body-color;
  width: $bullet-size;
  height: $bullet-size;
  border-radius: 50%;
  margin: $bullet-margin 0;
  opacity: 0.75;
  transition: 0.3s height ease-out, 0.3s width ease-out, 0.3s opacity ease-out;

  &.active {
    width: $bullet-active-size;
    height: $bullet-active-size;
    opacity: 1;
  }
}

.panel__inner {
  width: 100%;
  display: flex;
}

.panel__center {
  // justify-self: center;
  // @include margin(top,2)
  margin-top: auto;
  margin-bottom: auto;
  @include media-breakpoint-up(sm) {
    margin-top: auto;
    margin-bottom: auto;
  }

}

.panel__bottom {
  align-self: flex-end;
  width: 100%;
  @include padding(bottom,2);
}
.panel__heading {
  @include margin(bottom,1);
  font-size: $h4-font-size;
  @include media-breakpoint-up(sm) {
    font-size: $h2-font-size;
  }

}

/*
h2.panel__heading {
  font-size: $h4-font-size;
  @include media-breakpoint-up(sm) {
    font-size: $h2-font-size;
  }
}
*/

h4.panel__heading {
  font-weight: bold;
  font-size: $font-size-base;
  @include margin(bottom,.5);
}

.panel__heading--lg {

  font-size: $h3-font-size;
  @include margin(bottom,1);

  @include media-breakpoint-up(sm) {
    font-size: $h2-font-size;
  }

  @include media-breakpoint-up(xl) {
    font-size: $h1-font-size;
  }
}


.panel__content {
  flex: 0 0 percentage(8/12);
  width: percentage(8/12);
  padding-right: $grid-gutter-width;
  @include media-breakpoint-up(sm) {
    flex: 0 0 percentage(7/12);
    width: percentage(7/12);
  }
  @include media-breakpoint-up(xl) {
    flex-basis: percentage(6/12);
    width: percentage(6/12);
  }
}

.panel__content--offset {
  @include media-breakpoint-up(sm) {
    margin-left: 80px;

  }
}




.panel__content.panel--columns {
  display: flex;
  flex-flow: row wrap;
  width: 100%;
  flex: 0 0 100%;

  // margin-left: -$grid-gutter-width / 2;
  // margin-right: -$grid-gutter-width / 2;
  justify-content: center;
  align-items: flex-start;
  padding-right: 0;
  margin-left: -$grid-gutter-width / 2;
  margin-right: -$grid-gutter-width / 2;
  @include media-breakpoint-up(sm) {

  }

  .panel__image {
    min-height: 100px;
    line-height: 0;
    img {
    vertical-align: middle;
    }

    @include media-breakpoint-up(sm) {
      min-height: 140px;
      line-height: 140px;
    }
  }
  // align-content: flex-end; //center;
}

.panel__content--column {
  padding-left: $grid-gutter-width / 2;
  padding-right: $grid-gutter-width / 2;
  flex: 1 0 100%;
  display: flex;
  @include margin(bottom,2);


  @include media-breakpoint-up(sm) {
    flex: 1 0 percentage(1/4);
    text-align: center;
    flex-flow: column nowrap;
    @include margin(bottom,0);
  }

  .panel__image {
    flex: 0 0 20%;
    margin-right: $grid-gutter-width / 2;
    @include media-breakpoint-up(sm) {
      margin-right: 0;
    }
  }
}


.panel--intro {
  @include margin(top,5);
}


.snippet--ci {
  margin-left: -10px;
  // max-width: 100%;
  @include margin(bottom,1);
  @include media-breakpoint-up(sm) {
    max-width: 70%;
    margin-left: -40px;
  }
}

.snippet--content {
  margin-left: -10px;
  margin-right: -170px;
  @include margin(bottom,3);

  @include media-breakpoint-up(xl) {
    margin-right: -230px;
  }
}

.snippet--content--animated {
  width: 920px;
  height: 240px;
  @include margin(top,2);
  background-size: contain;
  background-image: url(../el/snippets_content.png);
  background-repeat: repeat-x;
  margin-left: -180px;
  animation: slidecontent 10s linear infinite;
}

@keyframes slidecontent {
  from {
    background-position: 0px center;
  }
  to {
    background-position: 920px center;
  }
}



