@import "../utils/variables";
@import "../utils/mixins";

.phone--xs {
  position: fixed;
  right: 0;
  left: auto;
  top: 100px;
  // top: 50%;
  // transform: translateY(-50%);
  width: 120px;
  height: 462px;


  @include media-breakpoint-up(sm) {
    display: none;
  }

  &:before {
    top: 1%;
    right: -15%;
    bottom: 4%;
    border-radius: 30px;
    left: 2%;
    content: "";
    // background-color: #900;
    position: absolute;
    box-shadow: 0 10px 20px fade-out(#000,0.6);
  }
}


.swiper--phone--screens--xs {
  // background-color: $white;
  width: 95%;
  height: 83.2%;
  top: 7.3%;
  left: 2.5%;
  .swiper-slide {
    background-color: $gray-400;
  }

  .slideInfo {
    @include padding(x,1);
    @include padding(top,1);


    .small {
      font-size: 0.8rem;
      font-variant-caps: all-small-caps;
    }
    h3 {
      font-size: $font-size-base;
    }
  }
}