@import "../utils/variables";
@import "../utils/mixins";

#screen--backend {
  display: none;
  position: fixed;
  z-index: $zindex-fixed;
  left: 50%;
  top: 100%;
  pointer-events: none;
  width: 340px;
  transform: translateX(-170px);
  @include media-breakpoint-up(sm) {
    transform: translateX(-470px);
    width: 550px;
    display: block;
  }
  @include media-breakpoint-up(xl) {
    transform: translateX(-560px);
    width: 700px;
  }
}


.screenBackend__shader {
  @include fill-abs;
  background-image: linear-gradient(fade-out($bg-blue,1) 25%, fade-out($bg-blue,0.2) 75%); // $blue;
  z-index: 10;
  opacity: 0;
}

.panel--backend {

  .panel__inner {
    justify-content: flex-end;
    @include media-breakpoint-up(sm) {

    }
  }
  .panel__content {
    @include padding(right,0);
    flex-basis: percentage(10/12);
    @include media-breakpoint-up(sm) {
      flex-basis: percentage(4/12);
    }
  }

  .panel__center {
    // @include margin(bottom,4);
    margin-top: 250px;
    @include media-breakpoint-up(sm) {
      margin-top: auto;
    }
  }
}









.screenBackend__mixesContainer,
.screenBackend__cardsContainer,
.screenBackend__mixTitleContainer {
  // background-color: fade-out($red,0.5);
  position: absolute;
  // width: 100px;
  // height: 100px;
  overflow-y: hidden;
// background-color: fade-out($orange,0.6);
}

.screenBackend__mixesContainer {
  top: 15%; // 65px;
  left: 0.3%; // 2px;
  width: 44.5%; // 312px;
  height: 85%; // 373px;
}

.screenBackend__dimmer {

  background-color: $body-color;
  opacity: 0.6;
  @include fill-browser;
}

.screenBackend__mixTitleContainer {
  top: 7.4%; // 33px;
  left: 45.3%; // 318px;
  width: 54.1%; // 372px;
  height: 22.2%; // 97px;
  @include padding(x,1);
  @include padding(top,1);
}

.screenBackend__cardsContainer {
  top: 39.4%; // 173px;
  left: 44.7%; // 318px;
  width: 53.5%; // 374px;
  height: 60.3%; // 265px;
}


.screenBackend__content {

  font-family: $font-family-phoneui;
  // @include fill-browser;

  .lipsum-bars {
    line-height: 10px;

    &.title {
      line-height: 8px;
      margin-bottom: 4px;
      .lipsum-bar {
        background-color: $gray-600;
        height: 5px;

        &:nth-child(odd) {
          width: 60%;
        }

        &:nth-child(even) {
          width: 70%;
        }
      }
    }

    &.excerpt {
      .lipsum-bar {
        background-color: $gray-400;
      }
    }

  }
  .lipsum-bar {
    height: 4px;
    border-radius: 1px;
    &.tagline {
      width: 40px;
      background-color: $gray-400; // $orange;
    }
    &.username {
      width: 60px;
    }
  }

}


.screenBackend__card--mix {
  @include margin(x,0.75);
  @include margin(top,1);
  display: flex;
  justify-content: stretch;

  & + .screenBackend__card--mix {
    border-top: 1px solid $gray-300;
    @include padding(top,1);
  }

  &.active {
    background-color: desaturate(lighten($blue,32%),3%);
    @include margin(left,0);

    @include padding(left,0.75);
    @include padding(top,0.5);
    @include padding(bottom,0.75);
    border-radius: 0 $border-radius $border-radius 0;

    .lipsum-bars {
      &.excerpt {
        .lipsum-bar {
          background-color: fade-out(#000,0.7);
        }
      }
      &.title {
        .lipsum-bar {
          background-color: fade-out(#000,0.5);
        }
      }
    }

    & + .screenBackend__card--mix {
      border-top: none;
      margin-top: 0;
      // @include padding(top,1);
    }
  }
}

.bCardMix__thumbnail {
  flex: 0 0 60px;
  margin-right: 10px;
  margin-top: 5px;
}

.bCardMix__content {
  flex: 1 0 auto;
}






.bMixTitle__user {
  display: flex;
  align-items: center;
}


.bMixTitle__avatar {
  width: 10px;
  height: 10px;
  margin-right: 10px;
}


.bMixTitle__title {
  color: $gray-600;
  font-weight: bold;
  font-size: 36px;
  margin-top: 5px;
}


/*
  _________________________________________________________

  //! Cards

*/
$cardBorderRadius: 4px;

.screenBackend__card {
  @include margin(left,0.75);
  @include margin(bottom,0.5);
  display: flex;
  justify-content: stretch;
  background-color: $white;
  border-radius: $cardBorderRadius;
  box-shadow: 1px 1px 1px -1px fade-out(#000,0.7);
  margin-right: 2px;

  .bCard__content {
    .tchopCard__socialHeader {
      align-items: center;
      margin-bottom: 0; // 0.5rem;
      @include margin(top,0.25);
      .icon--social {
        width: 22px;
        margin-left: -5px;
      }
    }
    .tchopCard__socialAuthor {
      line-height: 4px;
      .lipsum-bar {
        height: 4px;
      }
    }
  }
}



.bCard__thumbnail {
  flex: 0 0 120px;
  margin-top: 0;
  & > img {
    border-radius: $cardBorderRadius 0 0 $cardBorderRadius;
  }
}

.bCard__content {
  flex: 1 0 auto;
  @include padding(bottom,1);
  @include padding(x,1);
}



.backendCard--facebook,
.backendCard--twitter {
  .excerpt {
    margin-left: 28px;
  }
}



/*
  _________________________________________________________

  //! Meta

*/

$meta-border-width: 4px; // 2px;
$meta-arrow-size: $meta-border-width * 6;

$meta-color: $yellow; // lighten($blue,20%); // $white;

.metaLayer {
  font-family: $font-family-meta;
  // font-style: italic;
  color: $meta-color;
  pointer-events: none;
  font-weight: normal; // 600;
  line-height: 0.9;
  font-variant: all-small-caps;
  font-size: $font-size-base * 1.3;
  display: flex;
  @include margin(top, 0.5);
  position: absolute;
  opacity: 0;
  // justify-content: center;


  .meta__arrow {
    text-align: center;
    position: relative;
    // background-image: url(../../shared/el/shape__arrow_01.svg);
    // background-size: contain;
    // background-position: center bottom;
    // background-repeat: no-repeat;
    @include padding(x,1.5);
    @include padding(bottom,1.9);
     @include padding(top,0.25);
  }
  .svgArrow {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    // transform: translateX(-25px) translateY(-10%);

    .arrow {
      stroke: $meta-color;
      stroke-width: $meta-border-width;

    }
  }
}




/*
  _________________________________________________________

  //! Integrations

*/


.screenBackend__integrations {
  width: 440px;
  position: absolute;
  // top: 40px; // -20px;
  bottom: 167px;
  left: 120px; // -30px;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  align-content: center;

  .intIcon {
    flex: 0 1 percentage(1/4);
    opacity: 0;
  }

  .intArrow {
    position: absolute;
    top: 110%;
    opacity: 0;
    img {
      width: 160px;
      height: auto;
    }
  }

  .metaLayer {
    top: 230px;
  }
}


/*
  _________________________________________________________

  //! slack

*/


.screenBackend__slackBrowser {
  position: absolute;
  // top: -30px; // -40px;
  bottom: 170px;
  left: 160px; // -40px;
  opacity: 0;

  .slackArrow {
    position: absolute;
    left: 50%;
    top: calc(100% - 40px);
    transform: translateX(-50%);
    img {
      width: 160px;
      height: auto;
    }

  }

  .meta__arrow {
    &:nth-child(1) {
      left: 60px;
    }
    &:nth-child(2) {
      left: 160px;
    }
  }
}



/*
  _________________________________________________________

  //! User Management

*/

.screenBackend__users {
  @include fill-browser;
  background-color: $gray-300;
  // transform: translateX(100%);
  opacity: 0;
  overflow-y: hidden;
  color: $gray-700;

  .lipsum-bar {
    height: 6px;
    background-color: $gray-400;
  }

  .lipsum-bars {
    line-height: 0.9;
  }
}

.screenBackend__aside,
.screenBackend__userList
 {
  @include padding(x,2);
  @include padding(top,1);
}

.screenBackend__pageTitle {
  font-size: $h3-font-size;
  font-weight: bold;
}


.screenBackend__addUserForm {

  // $border-radius: 10px;

  display: flex;
  @include margin(top,1.5);
  // box-shadow: 1px 1px 2px #000;

  .input,.button {
    height: 24px;

  }
  .input {
    background-color: $gray-100;
    flex: 1 0 auto;
    border-radius: $border-radius 0 0 $border-radius;
  }
  .button {
    background-color: $green;
    text-transform: uppercase;
    color: #fff;
    font-size: 10px;
    font-weight: bold;
    @include padding(x,0.5);
    line-height: 24px;
    vertical-align: middle;
    border-radius: 0 $border-radius $border-radius 0;
  }
}

.screenBackend__userList {
  background-color: $gray-100;
  @include padding(top,2);
}

.screenBackend__userCard {
  @include margin(bottom,1);
  @include padding(bottom,1);
  border-bottom: $light-border;
  display: flex;
  align-items: flex-start;
  // justify-content: space-around;
}

.userCard__avatar {
  flex: 0 0 40px;
  .img__avatar {
    width: auto;
    height: auto;
    max-width: 100%;
  }
}
.userCard__content {
  flex: 1 1 auto;
  @include padding(left,1);
  @include padding(right,5);
  .lipsum-bar:first-child {
    background-color: $gray-500;
  }
}

.userCard__meta {
  flex: 0 1 60px;
}

// animate avatars

.userCard__avatar {
  #icon--check {
    transition: 0.2s opacity ease-out 0.3s;
  }
  #oval-user, #shape-user {
    transition: 0.3s fill ease-out;
  }

}

.screenBackend__userCard {
  &.inactive {


    .userCard__avatar {
      #icon--check {
        opacity: 0;
      }

      #oval-user {
        fill: $gray-400;
      }

      #shape-user {
        fill: $gray-400;
      }
    }
  }
}


