@import "../utils/variables";
@import "../utils/mixins";

.panel--cases {

  .swiper-pagination {
    position: relative;
    // text-align: right;
    // justify-content: flex-end;
  }

  .panel__bottom {
    .panel__inner {
      justify-content: flex-end;
      @include margin(top,2);
    }
  }

}



.container__cases {

  // text-align: right;
  flex: 1 1 100%; // percentage(2/3);

  @include margin(left,-2);

  @include media-breakpoint-up(sm) {
    padding-right: 320px;
  }
  .cases__footer {
    @include margin(top,2);
  }

  h6 {
    @include margin(bottom,1);
  }
}

.useCases__menu {
  font-size: $font-size-sm;
  @include margin(x,-0.5);
  @include margin(top,-0.5);
  display: flex;
  flex-flow: column nowrap;
  & > a {

    @include padding(x,0.5);
    @include padding(y,0.5);
    border-radius: $border-radius;

    &:hover {
      text-decoration: none;
      background-color: darken($widget-color,10%);
      color: #fff;
    }

    .tagline {

    }
    .title {

    }

    &.active {
      font-weight: bold;
    }
  }
}

.useCasesWrapper {
  background: linear-gradient(120deg, $widget-color, lighten($widget-color,10%)); // linear-gradient(120deg, $gray-200, $white);
  @include margin(bottom,4);
  @include padding(top,2);
  @include padding(bottom,1);
  @include padding(left,2);
  @include padding(right,6);
  color: $white;
  border-radius: $border-radius-lg;
  a {
    color: $white;
  }
  .swiper-pagination-bullet {
    background-color: fade-out($white, 0.2);
  }
  .swiper-pagination-bullet-active {
    background-color: $white;
  }

  .widget__title {
    color: $white;
    font-weight: 900;
    font-size: $font-size-base;
    @include margin(bottom,2);
  }
}

.useCase {



  font-size: $font-size-sm;



  .link--more {
    color: inherit;
  }


  ul.features {
    list-style-type: none;
    padding-left: 0;

    & > li {
      position: relative;
      @include padding(left, 1.5);
      @include margin(bottom, 0.5);
      &:before {
        content: "";
        position: absolute;
        left: 0;
      }
    }
  }

  .small, small {

  }
}



.useCase__logo {
  @include margin(bottom,1);
  @include padding(bottom,0.25);
  // width: 120px;
  border-bottom: 1px solid fade-out($body-bg,0.2);
  svg {
    width: 120px; // 100%;
    height: auto;
  }
  path {
    fill: $body-bg;
  }

  &.logo--fa {
    svg {
      width: 140px;
    }
  }
  &.logo--dfv {
    svg {
      width: 140px;
    }
  }
}

.useCase__title {
  // font-weight: 200; // bold;
  font-size: $font-size-lg;
  @include margin(bottom,1);

  @include media-breakpoint-up(sm) {
    font-size: $h2-font-size;
  }
}

.useCase__tagline {
  @include small-caps;
  font-weight: bold;
}

.useCase__description {
  font-size: $font-size-sm;
  @include media-breakpoint-up(sm) {
    font-size: $font-size-base;
  }
}





.swiper-pagination {
  display: flex;
  align-items: center;
  margin-left: -4px;
  margin-right: -4px;
}

.swiper-pagination-bullet {
  background-color: $body-color;
  opacity: 0.8;
  width: 4px;
  height: 4px;
  margin: 0 4px;

  &.swiper-pagination-bullet-active {
    width: 8px;
    height: 8px;
  }
}