@import "../utils/variables";
@import "../utils/mixins";

#screen--backend--xs {
  position: fixed;
  left: 50%;
  top: 100%;
  width: 350px;
  transform: translateX(-50%);
  z-index: $zindex-fixed - 1;


  @include media-breakpoint-up(sm) {
    display: none;
  }

  .screenBackend__integrations--xs {
    position: absolute;
    top: 5%;
    left: 55%;
    transform: translateX(100vw);
  }

  .screenBackend__users--xs {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
  }

  .screenBackend__slack--xs {
    position: absolute;
    top: 10%;
    left: 35%;
    width: 220px;
    border-radius: 4px;
    transform: translateX(100vw);
    box-shadow: -5px -5px 20px -5px fade-out(#000,0.6), -1px -1px 3px -1px fade-out(#000,0.6);
  }

}