@import "../utils/variables";
@import "../utils/mixins";

.phoneSlideWrapper {

  &:after {
    content: "";
    min-height: 1px;
    display: block;
    width: 100%;
    margin-top: -1px;
  }
}


body {

  // transition: .5s background-color ease-out;



  .bodyBackgrounds,
  .headerBackgrounds {
    .bg {
      transition: 1s opacity ease-out;
      position: fixed;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      opacity: 0;
      z-index: -1;

      &.white {
        background-color: $white; // lighten($gray-200,2%); // $white;
      }
      &.yellow {
        background-color: $yellow;
      }

      &.dark {
        background-color: $gray-800;
      }

      &.blue {
        background-color: $bg-blue;
      }
    }
  }

  .headerBackgrounds,
  .headerBackgrounds {
    .bg {
      position: absolute;
    }
  }


  .siteLogo {
    g, path {
      transition: .5s fill ease-out;
    }
  }

  &[data-bodybackground=yellow] {
  // &.bg--yellow {
    // background-color: $yellow;

    .bodyBackgrounds,
    .headerBackgrounds {
      .bg.yellow {
        opacity: 1;
      }
    }

    .siteHeader {
      // background-color: $yellow;
    }

    .siteLogo {
      #el2 {
        fill: #fff;
      }
    }

    .link--more {
      color: $body-color; // darken($red,10%);
      font-weight: bold;
    }
  }



  &[data-bodybackground=white] {
  // &.bg--white {
    // background-color: $white;
    .bodyBackgrounds,
    .headerBackgrounds {
      .bg.white {
        opacity: 1;
      }
    }
    .siteHeader {
      // background-color: $white; // $gray-100;

    }
  }

  &[data-bodybackground=dark] {
  // &.bg--yellow {
    $col: $gray-800;
    // background-color: $col;
    color: $body-bg;

    .panel a {
      color: $body-bg;
    }

    .bodyBackgrounds,
    .headerBackgrounds {
      .bg.dark {
        opacity: 1;
      }
    }
    .siteHeader {
      // background-color: $col; // $col;
      color: $body-bg;
      a {
        color: $body-bg;
      }
      .bar {
        background-color: $body-bg;
      }
      &:after {
        opacity: 1;
      }
    }
    .siteLogo {
      #el4,
      #type {
        fill: $body-bg;
      }

    }

    .panelPagination__bullet {
      background-color: $white;
    }

    .nextPageLink {
      color: $white;
      &:hover {
        color: $yellow;
      }
    }
  }


  &[data-bodybackground=blue] {
    // &.bg--yellow {
      $col: $gray-800;
      // background-color: $col;
      color: $body-bg;

      .panel a {
        color: $body-bg;
      }

      .bodyBackgrounds,
      .headerBackgrounds {
        .bg.blue {
          opacity: 1;
        }
      }
      .siteHeader {
        // background-color: $col; // $col;
        color: $body-bg;
        a {
          color: $body-bg;
        }
        .bar {
          background-color: $body-bg;
        }
        &:after {
          opacity: 1;
        }
      }
      .siteLogo {
        #el4,
        #type {
          fill: $body-bg;
        }

      }

      .panelPagination__bullet {
        background-color: $white;
      }
    }



}