@import "../utils/variables";
@import "../utils/mixins";

.appicon {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  position: relative;
}

.appicon__title {
  color: $white;
  font-family: $font-family-phoneui;
  font-weight: 500;
  font-size: $font-size-sm;
  margin-top: -5px; // 6px;
}

.appicon__font {
  font-family: $font-family-phoneui; // $font-family-monospace;
  color: #fff;
  font-size: 24px;
  position: absolute;
  transform: translateY(-260px);
  text-align: center;
  line-height: 1.1;
  font-weight: 900;
  width: 330px;
  letter-spacing: -0.05em;
  .line {
    white-space: nowrap;
  }
  .letter {
    display: inline-block;
  }
}

.appicon__colors {
  position: absolute;
  transform: translateY(-260px);
  font-family: $font-family-phoneui;
  font-weight: 500;
  font-size: $font-size-sm * 0.8;
  display: flex;
  color: fade-out($black,0.3);

  .appicon__color {
    background-color: $white;
    border-radius: 8px;
    padding: 5px 5px 10px;
    margin: 0 10px;
  }
  .appicon__color--yellow {
    .colorpanel {
      background-color: $gray-200; // $yellow;
    }
  }
  .appicon__color--red {
    .colorpanel {
      background-color: $orange;
    }
  }
  .colorpanel {
    width: 80px;
    height: 40px;
    border-radius: 3px;
    margin-bottom: 3px;
  }

}

svg.img--appicon {

  z-index: 10;

  #appicon__branding {
    transform: translateX(25%) translateY(25%);
  }

}