@import "../utils/variables";
@import "../utils/mixins";

.lipsum-bars {

  line-height: 1.1;

  @include media-breakpoint-up(sm) {
    line-height: 1.5;
  }

  .lipsum-bar {
    &:nth-child(even) {
      width: 90%;
    }
  }

  $radio-size: 20px;
  $radio-size-xs: 16px;

  &.radio {
    margin-left: $radio-size-xs * 2;
    @include margin(bottom,1);
    @include media-breakpoint-up(sm) {
      margin-left: $radio-size * 2;
    }
    &:before {
      content: "";
      display: inline-block;
      width: $radio-size-xs;
      height: $radio-size-xs;
      border: $radio-size-xs / 5 solid $body-color;
      border-radius: 50%;
      position: absolute;
      margin-left: -$radio-size-xs * 2;
      margin-top: 4px;
      @include media-breakpoint-up(sm) {
        width: $radio-size;
        height: $radio-size;
        border: $radio-size / 5 solid $body-color;
        margin-left: -$radio-size * 2;
      }
    }

    &.active {
      &:before {
        background-color: $blue;
        box-shadow: inset 0 0 0 $radio-size / 5 #fff;
      }
    }

    .lipsum-bar {}

  }
}

.lipsum-bar {
  display: inline-block;
  width: 95%;
  height: 8px;
  border-radius: 1px;
  background-color: fade-out($body-color,.7);

  @include media-breakpoint-up(sm) {
    height: 10px;
    border-radius: 2px;
  }
}
.tagline .lipsum-bar {
  background-color: $orange;
  width: 30%;
  height: 7px;
}